<template>
    <div class="headPortrait">
      <form enctype="multipart/form-data" ref="uploadForm">
        <p class="file">选择文件
          <input class="uploadBotton" type="file" name="file" @change="toUploadImg" ref="toUploadImg"/>
        </p>
      </form>
        <img  :src="uploadImg" class="uploadImgB">
        <img  :src="uploadImg" class="uploadImgM">
        <img  :src="uploadImg" class="uploadImgS"><br />
        {{srcName}}
        <br />
        <p>
          <input class="toUpload" type="button" value="上传" @click="doUpload" />
        </p>
    </div>
</template>

<script>
  import {getNickName} from '../common/js/url'
  export default {
    created(){
      getNickName().then((res) =>{
        this.userId = res.result.usrId
      })
    },
    data(){
      return{
        uploadImg:"",  //上传的图片
        srcName:"",     //图片后缀
        userId:""
      }
    },
    methods:{
      toUploadImg(){
        var _this = this
        var file = this.$refs.toUploadImg.files[0];
        if(file.size/1024 > 256 ){                            //file.size 单位为byte
          this.$Message.error("图片太大，请选择小于256KB的图片");
          this.$Message.config({
            top: 50,
            duration: 4
          });
          return
        }
        this.srcName = file.name
        //判断文件类型
        var type=(this.srcName.substr(this.srcName.lastIndexOf("."))).toLowerCase();
        if(type!=".jpg"&&type!=".gif"&&type!=".jpeg"&& type!=".png"){
          this.$Message.error("您上传图片的类型不符合(.jpg|.jpeg|.gif|.png)！");
          this.$Message.config({
            top: 50,
            duration: 4
          });
          this.srcName = ""
          return ;
        }

        var reader = new FileReader();
        // 将图片将转成 base64 格式
        reader.readAsDataURL(file);
        reader.onloadstart = function () {
          console.log("开始读取....");
        }
        reader.onprogress = function () {
          console.log("正在读取中....");
        }
        reader.onabort = function () {
          console.log("中断读取....");
        }
        reader.onerror = function () {
          console.log("读取异常....");
        }
        // 读取成功后的回调
        reader.onloadend = function () {
          console.log("读取成功....");
          _this.uploadImg  = this.result
        }
      },
      doUpload(){
        var _this = this
        var formData = new FormData();
        var pic = this.$refs.uploadForm[0].files[0]
        formData.append('file',pic)
        formData.append('userId',this.userId)
        $.ajax({
          url: './uploadAvatar' ,
          type: 'POST',
          data: formData,
          async: false,
          cache: false,
          processData: false,
          contentType: false,
          dataType: "json",
          success: function (returndata) {
            console.log(returndata)
            console.log("success")
            _this.$Message.success("上传成功！");
            _this.$Message.config({
              top: 50,
              duration: 4
            });
          },
          error: function (XMLHttpRequest, textStatus, errorThrown) {
            console.log(XMLHttpRequest.status);
            console.log(XMLHttpRequest.readyState);
            console.log(textStatus);
            console.log("fail")
            _this.$Message.error("上传出错！");
            _this.$Message.config({
              top: 50,
              duration: 4
            });
          }
        });
      }
    },
    deactivated(){
      this.$destroy(true)
    }
  }
</script>
<style>
  .file{
    height: 30px;
    width: 85px;
    background: #fff;
    border: 1px solid #2d8cf0;
    border-radius: 4px;
    padding: 4px 12px;
    overflow: hidden;
    color: #495060;
    line-height: 20px;
    position: relative;
    margin: 0 auto;
  }
  .uploadImgB{
    height: 90px;
    width: 90px;
    margin-top: 20px;
  }
  .uploadImgM{
    height: 60px;
    width: 60px;
    margin-top: 20px;
  }
  .uploadImgS{
    height: 29px;
    width: 29px;
    margin-top: 20px;
  }
  .file input {
    position: absolute;
    height: 30px;
    width: 60px;
    right: 0;
    top: 0;
    opacity: 0;

  }
  .toUpload{
    height: 30px;
    width: 45px;
    background: #fff;
    border: 1px solid #2d8cf0;
    border-radius: 4px;
    padding: 4px 8px;
    overflow: hidden;
    color: #495060;
    line-height: 20px;
  }
</style>



